<template>
  <button
    class="btn"
    style=" width: 90px;height: 90px"
    data-bs-toggle="tooltip"
  >
    <!-- <img
      style="max-width: 100%;height: auto;"
      :src="require(`@/assets/diablo2/images/item/${itemCategory.name}.png`)"
    />
    <br /> -->
    {{ itemCategory.nameKo }}
  </button>
</template>

<script>
import itemCategoryList from "@/assets/diablo2/data/itemCategory.json";

export default {
  props: { itemCategoryName: String },
  data() {
    return {
      itemCategoryList,
    };
  },
  setup(props) {
    // itemCategory set
    var targetItemCategory = null;
    for (var i = 0; i < itemCategoryList.length; i++) {
      if (itemCategoryList[i].name === props.itemCategoryName) {
        targetItemCategory = itemCategoryList[i];
        break;
      }
    }

    return { itemCategory: targetItemCategory };
  },
  methods: {},
};
</script>

