<template>
  <div>
    <h1>룬워드 검색</h1>
    <p>
      <a
        href="https://namu.wiki/w/%EB%A3%AC%EC%96%B4%20%EC%95%84%EC%9D%B4%ED%85%9C"
        target="_blank"
        >나무위키의 데이터</a
      >를 참고하여 작성되었습니다.<br />
      데이터는
      <a
        href="https://github.com/luversof/luversof.github.io/tree/main/diablo2/"
        target="_blank"
        >이곳</a
      >에 수정을 요청하면 됩니다.
    </p>

    <h2 @click="isShowRuneFilter = !isShowRuneFilter">
      룬 필터
      <font-awesome-icon
        :icon="isShowRuneFilter ? 'caret-square-up' : 'caret-square-down'"
      />
    </h2>
    <div
      class="collapse collapse-horizontal"
      :class="isShowRuneFilter ? 'show' : ''"
    >
      <p>선택한 룬들을 함께 사용한 룬워드 목록을 보여줍니다. (and 조건)</p>
      <button
        class="btn btn-dark btn-sm  m-1"
        :class="selectedRuneIdList.length > 0 ? '' : 'disabled'"
        @click="clearSelectedRuneIdList"
      >
        룬 필터 해제
      </button>
      <br />
      <RuneButton
        class="btn btn-outline-dark m-1"
        v-for="rune in runeList"
        :key="rune.id"
        :id="rune.id"
        :class="isSelectedRuneId(rune.id) ? 'active' : ''"
        @click="toggleSelectedRuneId(rune.id)"
      />
    </div>

    <h2 @click="isShowItemCategoryFilter = !isShowItemCategoryFilter">
      아이템 카테고리 필터
      <font-awesome-icon
        :icon="
          isShowItemCategoryFilter ? 'caret-square-up' : 'caret-square-down'
        "
      />
    </h2>
    <div
      class="collapse collapse-horizontal"
      :class="isShowItemCategoryFilter ? 'show' : ''"
    >
      <p>
        선택한 아이템 카테고리에 해당하는 룬워드 목록을 보여줍니다. (or 조건)
      </p>
      <button
        class="btn btn-dark btn-sm  m-1"
        :class="selectedItemCategoryNameList.length > 0 ? '' : 'disabled'"
        @click="clearSelectedItemCategoryNameList"
      >
        룬 필터 해제
      </button>
      <br />
      <ItemCategoryButton
        class="btn-outline-dark m-1"
        v-for="itemCategoryName in targetItemCategoryNameList"
        :key="itemCategoryName"
        :itemCategoryName="itemCategoryName"
        :class="isSelectedItemCategoryName(itemCategoryName) ? 'active' : ''"
        @click="toggleSelectedItemCategoryName(itemCategoryName)"
      />
    </div>

    <h2>룬워드 목록</h2>
    <input
      type="text"
      class="form-control"
      v-model="searchKeyword"
      placeholder="검색어를 입력하세요"
    />
    <RunewordList
      :selectedRuneIdList="selectedRuneIdList"
      :selectedItemCategoryNameList="selectedItemCategoryNameList"
      :searchKeyword="searchKeyword"
    />
  </div>
</template>

<script>
import runeList from "@/assets/diablo2/data/rune.json";
import RuneButton from "@/components/diablo2/RuneButton.vue";

import itemCategoryList from "@/assets/diablo2/data/itemCategory.json";
import ItemCategoryButton from "@/components/diablo2/ItemCategoryButton.vue";

import RunewordList from "@/components/diablo2/RunewordList.vue";

export default {
  components: { RuneButton, ItemCategoryButton, RunewordList },
  data() {
    return {
      runeList,
      selectedRuneIdList: [], // 선택한 runeId 목록
      isShowRuneFilter: true,
      itemCategoryList,
      targetItemCategoryNameList: [
        "melee weapon",
        "ranged weapon",
        "axe",
        "sword",
        "polearm",
        "staff",
        "mace",
        "chest armor",
        "helm",
        "shield",
      ], //메뉴로 보여줄 itemCategory 목록
      selectedItemCategoryNameList: [], // 선택한 itemCategory 목록
      isShowItemCategoryFilter: true,
      searchKeyword: "",
    };
  },
  methods: {
    // selectedRuneIdList에 runeId를 추가하거나 삭제
    toggleSelectedRuneId(runeId) {
      if (this.isSelectedRuneId(runeId)) {
        this.selectedRuneIdList.splice(
          this.selectedRuneIdList.indexOf(runeId),
          1
        );
      } else {
        this.selectedRuneIdList.push(runeId);
      }
    },
    isSelectedRuneId(runeId) {
      return this.selectedRuneIdList.indexOf(runeId) >= 0;
    },
    clearSelectedRuneIdList() {
      this.selectedRuneIdList.splice(0, this.selectedRuneIdList.length);
    },

    toggleSelectedItemCategoryName(itemCategoryName) {
      if (this.isSelectedItemCategoryName(itemCategoryName)) {
        this.selectedItemCategoryNameList.splice(
          this.selectedItemCategoryNameList.indexOf(itemCategoryName),
          1
        );
      } else {
        this.selectedItemCategoryNameList.push(itemCategoryName);
      }
    },
    isSelectedItemCategoryName(itemCategoryName) {
      return this.selectedItemCategoryNameList.indexOf(itemCategoryName) >= 0;
    },
    clearSelectedItemCategoryNameList() {
      this.selectedItemCategoryNameList.splice(
        0,
        this.selectedItemCategoryNameList.length
      );
    },
  },
};
</script>

<style></style>
